import React, {Component} from 'react';
import {
  Button,
  FormGroup,
  Label,
  InputGroupAddon,
  Input,
  InputGroup,
  Col,
  FormText
} from 'reactstrap';
import FaCommentingO from 'react-icons/lib/fa/commenting-o';
import {css} from 'react-emotion'

const h = css`
padding:16px;
`
const radius = css`
border-radius:50%;
margin :2px
`
class Personal extends Component {
  state = {};
  render() {
    return <div className="registry-form-content">
      <FormGroup row>
        <Label for="exampleFile" sm={3}>
          <img
            className={radius}
            height="50"
            width="50"
            src="https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif?imageView2/1/w/80/h/80"
            alt="1"/>
        </Label>
        <Col sm={9} className={h}>
          <InputGroup>
            <input type="file" name="file" id="exampleFile" multiple/>
          </InputGroup>
        </Col>
      </FormGroup>
      <FormGroup row>

        <Label for="username"  sm={3}>
          
        </Label>
        <Col sm={9}>
          <InputGroup>
            <Input name="username" placeholder=""/>
          </InputGroup>
          <FormText>4-16</FormText>
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label for="mobile" sm={3}>
          
        </Label>
        <Col sm={9}>
          <InputGroup>
            <Input name="mobile" placeholder=""/>
          </InputGroup>
          <FormText>.</FormText>
        </Col>
      </FormGroup>

      <FormGroup row>
        <Label for="email" sm={3}>
          
        </Label>
        <Col sm={9}>
          <InputGroup>
            <Input name="email" placeholder=""/>
          </InputGroup>
          <FormText>.</FormText>
        </Col>
      </FormGroup>
      <FormGroup check row>
        <Col sm={{
          size: 5,
          offset: 2
        }}>
          <Button></Button>
        </Col>
      </FormGroup>
    </div>;
  }
}

export default Personal;



