import React, { Component } from 'react';
import './Deck.css';
import IndexCard from '../card/IndexCard';
import DeckTitle from './DeckTitle';
import { CardDeck } from 'reactstrap';
class IndexDeck extends Component {
  state = {};
  render() {
    return (
      <div className="deck-c">
        <DeckTitle title="" />
        <CardDeck>
          <IndexCard
            title=""
            subTitle="online-learning"
            text=""
            to="/plateform"
            alt="card image cap"
          />
          <IndexCard
            title=""
            subTitle="technology"
            text=""
            to="/technology"
            alt="card image cap"
          />
          <IndexCard
            title="UED"
            subTitle=" User Experience Design"
            text=""
            to="/ued"
            alt="card image cap"
          />
        </CardDeck>
      </div>
    );
  }
}

export default IndexDeck;



