import React, {Component} from 'react';
import {Jumbotron} from 'reactstrap';
import {News} from '../Api';
import {css} from 'react-emotion'
const content = css `
// width:60%;
max-width:1000px;

margin: 0 auto;
padding:2%
background: rgba(222, 221, 221, 0.8);
& button{
    padding: 10px 37px;
    color: #cac8c8;
    border-radius: 30px;
    border: 0px;
    background: #303837;
    cursor: pointer;
}

`

class NewsDetail extends Component {
  constructor(props) {
    super(props);
    this.switch = this
      .switch
      .bind(this)
    this.toggle = this
      .toggle
      .bind(this)

    let id = this.props.match.params.id;
    let detail = News.get(id);
    this.state = {
      detail: detail,
      status: false,
      modal: false
    };
  }
  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }
  switch (e) {
      this.setState({
        status: !this.state.status
      });
      this.toggle();
  }
  state = {};
  render() {
    let detail = News.get(this.props.match.params.id);
    return (
      <div className={content}>
        <Jumbotron>

          <h1 className="display-4 text-justify">2017</h1>
          <p className="pl-1 lead text-justify">
            <small>2017.12.09</small>
          </p>
          <img height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
          <p className="lead">2003Domain Driven Design
            15 CQRSEDAUncle
            Bob2014 Martin FowlerJames
            LewisDDD IT</p>
          <hr className="my-2"/>
          <img height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
          <p>It uses utility classes for typgraphy and spacing to space content out within
            the larger container.</p>
          <p className="lead">2003Domain Driven Design
            15 CQRSEDAUncle
            Bob2014 Martin FowlerJames
            LewisDDD IT</p>
          <p className="lead">2003Domain Driven Design
            15 CQRSEDAUncle
            Bob2014 Martin FowlerJames
            LewisDDD IT</p>
          <p className="lead">2003Domain Driven Design
            15 CQRSEDAUncle
            Bob2014 Martin FowlerJames
            LewisDDD IT</p>
        </Jumbotron>
      </div>

    );
  }
}

export default NewsDetail;



